import { createStore } from 'vuex'

export default createStore({
  state: {
    count: 0,
    isUpload: false,
    isLogin: false,
    noteData: [],
  },
  mutations: {
    increment(state) {
      state.count++;
    },
    toggleUpload(state) {
      state.isUpload = true;
    },
    toggleLogin(state) {
      state.isLogin = true;
    },
    updateNoteData(state,noteData) {
      state.noteData = noteData;
    }
  },
  actions: {
  },
  modules: {
  }
})
